import { Component, ViewChild, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ProductService } from "../../providers/product.services";
import { ModalController } from '@ionic/angular';
import { AddAddressPage } from './modal/addAddress';


@Component({
  selector: 'page-address',
  templateUrl: 'address.html',
  styleUrls: ['./address.scss'],
})
export class AddressPage implements OnInit {
public isSearchEnable: Boolean = false

  public addressList: any=[]
  public cartId: any
  public enableCheckoutScreen: boolean = false
  constructor(
    private _ProductService: ProductService, public modalController: ModalController,
    private _Router: ActivatedRoute,public Router:Router
  ) { }




  public searchproductList:any=[]
   ngOnInit() {
    this.getAddress();
    this.cartId = this._Router.snapshot.params.cartId;
    console.log(this.cartId,"cartId")
  }

  getAddress() {
    this._ProductService.getAddress(localStorage.getItem('userId')).subscribe((data: any) => {
      this.addressList = data;
      console.log(this.addressList)
    });
  }

  async openAddAdressModal() {
    const modal = await this.modalController.create({
      component: AddAddressPage,

    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned !== null) {
        this.getAddress()
      }
    });

    return await modal.present();
  }



  public selectedEntry: any = {
    _id: ""
  }
  onSelectionChange(data,$event) {


    console.log($event.toElement.ariaChecked);

    if($event.toElement.ariaChecked == 'true'){

      this.selectedEntry = Object.assign({}, this.selectedEntry, data);
    }else{
      this.selectedEntry._id=''
    }
    console.log(this.selectedEntry, "select")
    if (this.selectedEntry._id) {
      this.enableCheckoutScreen = true
    }else{
      this.enableCheckoutScreen = false
    }
  }

  processedToCheckOutPage(){
    this.Router.navigateByUrl(`/checkout/${this.cartId}/${this.selectedEntry._id}`);
  }


  async openEditAdressModal(_addressData) {
    const modal = await this.modalController.create({
      component: AddAddressPage,
      componentProps: {
        addressData: _addressData,
        cartId:this.cartId
      }
    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned !== null) {
        this.getAddress()
      }
    });

    return await modal.present();
  }
}
