import { Component, OnInit, Input } from '@angular/core';
  import { ModalController, NavParams } from '@ionic/angular';
  import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
  import { ProductService } from "../../../providers/product.services";
  import { ToastController } from '@ionic/angular';
  import { UserData } from '../../../providers/user-data';

@Component({
    selector: 'modal-addresss',
    templateUrl: './addAddress.html'
})
export class AddAddressPage {


      @Input() addressData:any;
      @Input() cartId:String
      public editAddressForm:FormGroup
      public searchproductList:any=[]
     ngOnInit() {
          this.editAddressForm = new FormGroup({
              pincode: new FormControl('',[Validators.required,Validators.maxLength(6)]),
              city: new FormControl('',[Validators.required]),
              state: new FormControl('',[Validators.required]),
              area: new FormControl('',[Validators.required]),
              nearBy: new FormControl(''),
              customerName:new FormControl('',[Validators.required]),
              customerMobile:new FormControl('',[Validators.required]),
              address: new FormControl('',[Validators.required]),
              street: new FormControl(''),
              houseNo:new FormControl('',[Validators.required]),
          });

          console.log(this.addressData,"addressdata");

          if(this.addressData){
            this.editAddressForm.patchValue(this.addressData)
          }




this.editAddressForm.get("pincode").valueChanges.subscribe(pincode => {
   console.log('firstname value changed')
   console.log(pincode)
   if(JSON.stringify(pincode)){
     if(JSON.stringify(pincode).length == 6){
       this.getPinCodeDetails(JSON.stringify(pincode))
     }
   }
})



      }

      constructor(
          private modalController: ModalController,
          private _ProductService: ProductService,
          private toastController:ToastController,
          private _userData:UserData
      ) {
      }

      getPinCodeDetails(pincode){
        this._userData.getPinodeData(pincode).subscribe(async (data: any) => {
            console.log("pincode data ::",data)
            let address = (data)? (data[0].PostOffice) ? data[0].PostOffice[0]:{}:{}
            console.log(address,"address")
            this.editAddressForm.get("city").setValue(address.District)
            this.editAddressForm.get("area").setValue(address.Name)
            this.editAddressForm.get("state").setValue(address.State)

        });
      }


     async submit($event) {

      if (this.editAddressForm.invalid) {
        const toast = await this.toastController.create({
          message: 'Please Fill the Address Properly',
          duration: 3000,
          color:'warning',
          position: 'bottom',
          animated: true,
        });
        toast.present();
        return
      }


        if(JSON.stringify(this.editAddressForm.get("customerMobile").value).length != 10){
          const toast = await this.toastController.create({
            message: 'Mobile Number is not Valid',
            duration: 3000,
            color:'warning',
            position: 'bottom',
            animated: true,
          });
          toast.present();
          return
        }

        if(JSON.stringify(this.editAddressForm.get("pincode").value).length != 6){
          const toast = await this.toastController.create({
            message: 'Invalid Pincode',
            duration: 3000,
            color:'warning',
            position: 'bottom',
            animated: true,
          });
          toast.present();
          return
        }


          console.log($event);
          let payload = Object.assign({}, $event.value);
          // payload.userId = localStorage.getItem('userId');

          console.log(payload, "payload");

          if(this.addressData){
          this._userData.editAddress(this.addressData._id,payload).subscribe(async (data: any) => {
              const toast = await this.toastController.create({
                message: 'Saved Successfully',
                 duration: 3000,
                  color:'secondary',
                position: 'bottom',
                animated: true,
              });
              toast.present();
              this.closeModal()
            });
          }else{
            this.addAddress($event)
          }

      }

      addAddress($event) {
        console.log($event);
        let payload = Object.assign({}, $event.value);
        payload.userId = localStorage.getItem('userId');

        console.log(payload, "payload")
        this._ProductService.SaveAddress(payload).subscribe(async (data: any) => {
            console.log("data", data);
            const toast = await this.toastController.create({
                message: 'Address Added',
                 duration: 3000,
                color:'secondary',
                position: 'bottom',
                animated: true,
              });
              toast.present();
            this.closeModal()
        });
    }




      async closeModal() {
          const onClosedData: string = "Address Added";
          await this.modalController.dismiss(onClosedData);
      }



  }
